// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { BTN_TEXT, STATUS_CODE } from '@/utils/constantsFile';
import repairWarnToday from '@/views/warn/WarnMontor/repairWarnToday.vue';
import repairWarnHistory from '@/views/warn/WarnMontor/repairWarnHistory.vue';
import paramWarnToday from '@/views/warn/WarnMontor/paramWarnToday.vue';
import paramWarnHistory from '@/views/warn/WarnMontor/paramWarnHistory.vue';
// import paramWarn from '@/views/warn/WarnAnalyze/paramWarn.vue';
import SearchBox from '@/components/common/SearchBox';
// import alarmStatistics from '@/views/report/EquipmentMonitoring/alarmStatistics.vue';
// import multiDeviceComparison from '@/views/report/EquipmentMonitoring/multiDeviceComparison.vue';
// import efficiencyStatistics from '@/views/report/EquipmentMonitoring/efficiencyStatistics.vue';
import {
  deviceExceptionGetRealWarnDeviceCount
} from '@/api/warn';
export default {
  name: 'EquipmentMonitoring',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      active: 0,
      active2: 0,
      activeN: 0,
      type: 2,

      // 告警设备数
      warnCount: 0,
      // 正常设备树
      normalCount: 0,

      // 默认引入的组件名称
      currentView: 'repairWarnToday',
      tabs: [
        {
          name: '故障报警',
          views: 'repairWarnToday'
        },
        {
          name: '参数报警',
          views: 'paramWarnToday'
        }
      ],
      tabs2: [
        {
          name: '实时'
        },
        {
          name: '历史'
        }
      ],
      savedSearchCondition: []
    };
  },
  created() {
    // 调用列表查询接口
    this.getWarnCount();
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      console.log(type);
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
      this.active2 = 0;
      this.getWarnCount();
    },
    /**
     * @description: 切换设备tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle2(i, type) {
      this.getWarnCount();
      this.active2 = i;
      if (i === 0) {
        if (this.active === 0) {
          this.currentView = 'repairWarnToday';
        } else {
          this.currentView = 'paramWarnToday';
        }
      } else if (i === 1) {
        if (this.active === 0) {
          this.currentView = 'repairWarnHistory';
        } else {
          this.currentView = 'paramWarnHistory';
        }
      }
    },
    /**
     * @description:获取设备告警数
     * @param
     * @return:
     * @author: CH
     */
    async getWarnCount() {
      var warnType = '';
      if (this.active === 0) {
        warnType = 'WARNING';
      } else if (this.active === 1) {
        warnType = 'PARAMETER';
      }

      try {
        const res = await deviceExceptionGetRealWarnDeviceCount({
          deviceWarningRecordEnum: warnType
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.warnCount = res.rows.warnCount;
          this.normalCount = res.rows.normal;
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          // this.tablePages.total = res.total;
          // 加载动画消失
          // this.tableLoadings = false;
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        this.$refs.utilizationRateTable.exportTable();
      } else if (this.active === 1) {
        this.$refs.alarmStatistics.exportTable();
      } else if (this.active === 2) {
        this.$refs.efficiencyStatistics.exportTable();
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
      }
      this.findList();
    },
    findList() {
      this.$nextTick(() => {
        // if (this.active === 0) {
        //   this.$refs.newPage.findList();
        // } else if (this.active === 1) {
        //   this.$refs.historyPage.findList();
        // } else if (this.active === 2) {
        //   this.$refs.historyTotalPage.findList();
        // }
        if (this.active === 0) {
          if (this.active2 === 0) {
            this.$refs.repairWarnToday.findList();
          } else {
            this.$refs.repairWarnHistory.findList();
          }
        } else {
          if (this.active2 === 0) {
            this.$refs.paramWarnToday.findList();
          } else {
            this.$refs.paramWarnHistory.findList();
          }
        }
      });
    }
  },
  components: {
    // 加载未审核组件
    SearchBox,
    HeaderTitle,
    HeadOperationItem,
    repairWarnToday,
    repairWarnHistory,
    paramWarnToday,
    paramWarnHistory
    // paramWarn
    // alarmStatistics,
    // multiDeviceComparison,
    // efficiencyStatistics
  }
};
